<template>
    <div id="app">
        <div class="nav" v-if="$route.path != '/manage_test/login'">
            <h2 class="logo">后台管理系统</h2>
            <Menu class="menu" mode="horizontal" theme="light" :active-name="selectMenu" @on-select="changeMenu">
                <MenuItem name="公号托管">
                    <Icon type="ios-paper" />
                    公号管理
                </MenuItem>
                <MenuItem name="参数二维码">
                    <Icon type="ios-people" />
                    微信小工具
                </MenuItem>
                <MenuItem name="粉丝数据">
                    <Icon type="ios-stats" />
                    数据统计
                </MenuItem>
                <MenuItem name="消息管理">
                    <Icon type="ios-stats" />
                    客服消息
                </MenuItem>
                <MenuItem name="群发列表">
                    <Icon type="ios-stats" />
                    群发消息
                </MenuItem>
            </Menu>
        </div>
        <router-view/>
    </div>
</template>

<script>
import store from './store'
export default {
    data() {
        return {
            selectMenu: this.$route.name
        }
    },
    methods: {
        changeMenu(name) {
            this.$router.push({name: name})
        }
    },
    beforeMount() {
        // let routerName = sessionStorage.getItem("routerName");
        // if(this.$route.path == '/manage' && routerName) {
        //     this.selectMenu = routerName
        // }
        // this.$router.beforeEach((to, from, next) => {
        //     if(from.name == '登录') {
        //         sessionStorage.setItem("routerName", to.name);
        //         this.selectMenu = '公号托管'
        //     }else {
        //         this.selectMenu = routerName
        //     }
        //     next()
        // });
    },
    mounted(){
        this.$Message.config({
            top: 100,
            duration: 3
        });
    },
    watch: {
      $route(to, from) {
        if(this.$route.meta.index == 'tuoguan') {
          this.selectMenu = '公号托管'
        } else if(this.$route.meta.index == 'weixin') {
          this.selectMenu = '参数二维码'
        } else if(this.$route.meta.index == 'statistics') {
          this.selectMenu = '粉丝数据'
        } else if(this.$route.meta.index == 'msgView') {
          this.selectMenu = '消息管理'
        } else if(this.$route.meta.index == 'groupSending') {
          this.selectMenu = '群发列表'
        }
      }
    }
}
</script>


<style lang="less">
    #app {
        font-family: "Avenir", Helvetica, Arial, sans-serif;
        -webkit-font-smoothing: antialiased;
        -moz-osx-font-smoothing: grayscale;
        text-align: center;
        color: #2c3e50;
    }
    .nav {
        width: 100%;
        display: flex;
        flex-direction: row;
        position: fixed;
        top: 0;
        left: 0;
        border-right: 1px solid #DBDEE2;
        .logo {
            width: 225px;
            color: #fff;
            line-height: 60px;
            padding: 0 30px;
            background: #ff8c00;
        }
        z-index: 11111;
        .menu {
            width: 100%;
            padding-left: 20px;
            
        }
    }
    .wrapper {
        margin-top: 60px;
        margin-left: 200px;
        padding: 30px;
    }
</style>
