import Vue from "vue";
import Router from "vue-router";
import Login from "./views/Login.vue";

import Gonghao from './views/Gonghao.vue';
import WeixinTool from './views/WeixinTools.vue';
import Statistics from './views/Statistics.vue';
import msgView from './views/msgView.vue';
import groupSending from './views/groupSending.vue';


Vue.use(Router);

export default new Router({
  mode: "history",
  base: process.env.BASE_URL,
  routes: [{
    path: "/",
    redirect: '/manage_test'
  }, {
    path: "/manage_test",
    redirect: '/manage_test/login'
  }, {
    path: '/manage_test/login',
    name: '登录',
    component: Login
  }, {
    path: '/manage_test/home',
    name: '主页',
    redirect: '/manage_test/gonghao'
  }, {
    path: '/manage_test/gonghao',
    // name: '公号管理',
    component: Gonghao,
    redirect: '/manage_test/gonghao/tuoguan',
    children: [{
      path: 'tuoguan',
      name: '公号托管',
      component: () => import("@/components/gonghao/tuoguan.vue"),
      meta: {index: 'tuoguan', content: 'tuoguan'},
      children: [{
        path: 'create',
        name: '接管新公号',
        component: () => import("@/components/gonghao/tuoguan/editInfo.vue"),
        meta: {index: 'tuoguan', content: 'tuoguan'},
      }, {
        path: 'update',
        name: '修改公号',
        component: () => import("@/components/gonghao/tuoguan/editInfo.vue"),
        meta: {index: 'tuoguan', content: 'tuoguan'},
      }, {
        path: 'setting',
        name: '配置公号',
        component: () => import("@/components/gonghao/tuoguan/setting.vue"),
        meta: {index: 'tuoguan', content: 'tuoguan'},
        children: [{
          path: 'create',
          name: '配置新规则',
          component: () => import("@/components/gonghao/tuoguan/editSetting.vue"),
          meta: {index: 'tuoguan', content: 'tuoguan'},
        }, {
          path: 'update',
          name: '修改配置',
          component: () => import("@/components/gonghao/tuoguan/editSetting.vue"),
          meta: {index: 'tuoguan', content: 'tuoguan'},
        }]
      }]
    }, {
      path: 'menu',
      name: '配置菜单',
      component: () => import("@/components/gonghao/menuSetting.vue"),
      meta: {index: 'tuoguan', content: 'menu'},
      children: [{
        path: 'create',
        name: '新建菜单',
        component: () => import("@/components/gonghao/menu/editInfo.vue"),
        meta: {index: 'tuoguan', content: 'menu'},
      }, {
        path: 'update',
        name: '修改菜单',
        component: () => import("@/components/gonghao/menu/editInfo.vue"),
        meta: {index: 'tuoguan', content: 'menu'},
      }]
    }, {
      path: 'message',
      name: '回复消息',
      component: () => import("@/components/gonghao/message.vue"),
        meta: {index: 'tuoguan', content: 'message'},
        children: [{
        path: 'create',
        name: '创建回复消息',
        component: () => import("@/components/gonghao/message/editMessage.vue"),
        meta: {index: 'tuoguan', content: 'message'},
      }, {
        path: 'update',
        name: '编辑回复消息',
        component: () => import("@/components/gonghao/message/editMessage.vue"),
        meta: {index: 'tuoguan', content: 'message'},
      }]
    }, {
      path: 'timingMenu',
      name: '配置定时菜单',
      component: () => import("@/components/gonghao/timingMenu.vue"),
      meta: {index: 'tuoguan', content: 'timingMenu'},
      children: [{
        path: 'create',
        name: '新建定时菜单',
        component: () => import("@/components/gonghao/timingMenu/editInfo.vue"),
        meta: {index: 'tuoguan', content: 'timingMenu'},
      }, {
        path: 'update',
        name: '修改定时菜单',
        component: () => import("@/components/gonghao/timingMenu/editInfo.vue"),
        meta: {index: 'tuoguan', content: 'timingMenu'},
      }]
    }]
  }, {
    path: '/manage_test/weixinTool',
    // name: '微信工具',
    component: WeixinTool,
    redirect: '/manage_test/weixinTool/qrCode',
    children: [{
      path: 'qrCode',
      name: '参数二维码',
      component: () => import("@/components/weixinTools/qrCode.vue"),
      meta: {index: 'weixin', content: 'qrCode'},
      children: [{
        path: 'create',
        name: '创建二维码',
        component: () => import("@/components/weixinTools/qrCode/editInfo.vue"),
        meta: {index: 'weixin', content: 'qrCode'},
      }, {
        path: 'update',
        name: '编辑二维码',
        component: () => import("@/components/weixinTools/qrCode/editInfo.vue"),
        meta: {index: 'weixin', content: 'qrCode'},
      }]
    }, {
      path: 'tag',
      name: '标签管理',
      component: () => import("@/components/weixinTools/tag.vue"),
      meta: {index: 'weixin', content: 'tag'},
    }]
  }, {
    path: '/manage_test/statistics',
    // name: '数据统计',
    component: Statistics,
    redirect: '/manage_test/statistics/fans',
    children: [{
      path: 'fans',
      name: '粉丝数据',
      meta: {index: 'statistics', content: 'fans'},
      component: () => import("@/components/statistics/fans.vue")
    }]
  }, {
    path: '/manage_test/msg_view',
    // name: '客服消息',
    component: msgView,
    redirect: '/manage_test/msg_view/guanli',
    children: [{
      path: 'guanli',
      name: '消息管理',
      component: () => import("@/components/msgView/manage.vue"),
      meta: {index: 'msgView', content: 'guanli'},
      children: [{
        path: 'create',
        name: '添加客服消息',
        meta: {index: 'msgView', content: 'guanli'},
        component: () => import("@/components/msgView/manage/editInfo.vue")
      }, {
        path: 'update',
        name: '修改客服消息',
        meta: {index: 'msgView', content: 'guanli'},
        component: () => import("@/components/msgView/manage/editInfo.vue")
      }]
    }]
  }, {
    path: '/manage_test/groupSending',
    component: groupSending,
    redirect: '/manage_test/groupSending/groupList',
    children: [{
      path: 'groupList',
      name: '群发列表',
      component: () => import("@/components/groupSending/groupList.vue"),
      meta: {index: 'groupSending', content: 'groupList'},
      children: [{
        path: 'send',
        name: '发送消息',
        meta: {index: 'groupSending', content: 'groupList'},
        component: () => import("@/components/groupSending/sendMessage.vue")
      }]
    }]
  }]
});
